import { Component } from '@angular/core';
import { IonicPage, AlertController, NavController, LoadingController, NavParams, ToastController } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';
@IonicPage({
  name: 'page-add-cyrpto-amount',
  segment: 'add-cyrpto-amount'
})

@Component({
  selector: 'page-add-cyrpto-amount',
  templateUrl: 'add-cyrpto-amount.html'
})

export class AddCyrptoAmountPage {
  bankname: any;
  amount: number;
  cryptocurrency: any;
  reference: any;
  constructor(public navCtrl: NavController, public translate: TranslateService, public navparams: NavParams, public user: UserProvider, public alertCtrl: AlertController, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {
    this.translate.setDefaultLang('en');
    this.translate.use('es');
    this.bankname = this.navparams.get('bankname');
    this.cryptocurrency = this.navparams.get('crypto');
    console.log(this.cryptocurrency);

  }
  // check userCyrpto payment and
  next() {

    console.log(this.amount, 'Test plan');

    this.user.CryptoApi(this.amount, this.cryptocurrency).subscribe(
      data => {
        let datas = JSON.parse(JSON.stringify(data));
        let data2 = JSON.parse(datas._body);

        console.log(data);
        console.log(data2.data.result);
        if (data2.success) {

          this.navCtrl.setRoot('page-make-cyrpto-payment-summary', { 'data': data2.data.result });

        } else {

          this.translate.get('Add-cyrpto-amount-page').subscribe((translated: string) => {
            var next_btn_error_toast = this.translate.instant('Add-cyrpto-amount-page.list.error_toast');

            let toast = this.toastCtrl.create({
              message: next_btn_error_toast,
              duration: 3000,
              position: 'top'
            });

            toast.onDidDismiss(() => {
              console.log('Dismissed toast');

            });

            toast.present();

          });

        }
      }, error => {
        console.log(error.JSON);
        setTimeout(() => {
          this.navCtrl.setRoot('page-general_error');
        }, 1000);
      },
    )

  }



}



